.page {
  font-family: "Poppins", sans-serif;
  color: $main-text-color;
  line-height: 140%;

  scroll-behavior: smooth;

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transition: all $effectDuration;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }

}
